<template>
  <div class="page">
    <!-- <header></header> -->
    <div class="pagecontent">
      <div class="saveForm">
        <el-form
          ref="form"
          label-position="left"
          class="card"
          :label-width="$store.state.mobile ? '' : ''"
        >
          <!--eslint-disable-->
          <el-form-item label="   ">
            <el-date-picker
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
              v-model="searchMsg.startDate"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="   ">
            <el-date-picker
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
              v-model="searchMsg.endDate"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-input
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.dh"
              placeholder=""
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <!-- <el-autocomplete
              :highlight-first-item="true"
              ref="customerName"
              placeholder=""
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.startStation"
              :fetch-suggestions="(str,cb)=>{$querySearchAsync(str,cb,'startStation','startStation')}"
            ></el-autocomplete>-->
            <el-select
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.startStation"
              multiple
              filterable
              reserve-keyword
              placeholder=""
              @focus="$remoteMethod('','startStation','startStation',(arr)=>{startStationArr = arr})"
            >
              <el-option
                v-for="item in startStationArr"
                :key="item.startStation"
                :label="item.startStation"
                :value="item.startStation"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <!-- <el-autocomplete
              :highlight-first-item="true"
              ref="storeName"
              placeholder=""
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.endStation"
              :fetch-suggestions="(str,cb)=>{$querySearchAsync(str,cb,'endStation','endStation')}"
            ></el-autocomplete>-->
            <el-select
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.endStation"
              multiple
              filterable
              reserve-keyword
              placeholder=""
              @focus="$remoteMethod('','endStation','endStation',(arr)=>{endStationArr = arr})"
            >
              <el-option
                v-for="item in endStationArr"
                :key="item.endStation"
                :label="item.endStation"
                :value="item.endStation"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-autocomplete
              :highlight-first-item="true"
              ref="storeName"
              placeholder=""
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.customerName"
              :fetch-suggestions="(str,cb)=>{$querySearchAsync(str,cb,'customer','customerName')}"
            ></el-autocomplete>
          </el-form-item>
          <el-form-item label="">
            <el-autocomplete
              :highlight-first-item="true"
              ref="storeName"
              placeholder=""
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.consignee"
              :fetch-suggestions="(str,cb)=>{$querySearchAsync(str,cb,'consignee','consignee')}"
            ></el-autocomplete>
          </el-form-item>
          <el-form-item label="   ">
            <el-select
              v-model="searchMsg.payment"
              placeholder=""
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
            >
              <el-option
                v-for="item in [{label:'',value:''},{label:'',value:''},{label:'',value:''},{label:'',value:''}]"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="   ">
            <el-select
              v-model="searchMsg.state"
              placeholder=""
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
            >
              <el-option
                v-for="item in [{label:'',value:''},{label:'',value:0},{label:'',value:1}]"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-autocomplete
              :highlight-first-item="true"
              ref="storeName"
              placeholder=""
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.goods"
              :fetch-suggestions="(str,cb)=>{$querySearchAsync(str,cb,'goods','goods')}"
            ></el-autocomplete>
          </el-form-item>
          <el-form-item>
            <div style="width:100%;display:flex">
              <el-button type="primary" @click="refresh(true)"></el-button>
              <el-button type="warning" @click="changeType">{{dataType}}</el-button>
              <el-button
                type="success"
                @click="goInventory"
                v-if="dataType==='' && permission(['all','inventoryEdit'])"
              ></el-button>
              <exportExcel
                style="margin-left:10px"
                :format="format"
                :msg="selectData"
                v-if="dataType==='' && !$store.state.mobile"
              ></exportExcel>
              <el-button
                type="info"
                @click="dialogVisible = true"
                style="margin-left:10px"
                v-if="dataType===''"
              ></el-button>
              <el-dialog title="" :visible.sync="dialogVisible" width="30%" append-to-body>
                <el-checkbox-group v-model="checkList">
                  <el-checkbox
                    :label="item.name"
                    v-for="item in formatTable"
                    :key="item.type"
                    v-model="item.flag"
                  ></el-checkbox>
                </el-checkbox-group>
              </el-dialog>
            </div>
          </el-form-item>
        </el-form>
      </div>
      <el-table
        ref="multipleTable"
        :data="tableData"
        border
        height="250"
        class="card"
        key=""
        v-if="dataType === ''"
        @selection-change="handleSelectionChange"
        :row-class-name="$tableRowClassName"
        :cell-class-name="$tableCellClassName"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
          :prop="item.type"
          :label="item.name"
          v-for="item in tablecolumns"
          :key="item.type"
          :width="item.type === 'date' && 100"
        >
          <template slot-scope="scope">
            <div v-if="item.type === 'state'">
              <span>{{scope.row.state == 0 ? '' : ''}}</span>
            </div>
            <div v-else>
              <span>{{scope.row[item.type]}}</span>
            </div>
          </template>
        </el-table-column>

        <!-- <el-table-column prop="kddh" label=""></el-table-column>
        <el-table-column prop="dh" label=""></el-table-column>
        <el-table-column prop="date" label="" width="100"></el-table-column>
        <el-table-column prop="startStation" label=""></el-table-column>
        <el-table-column prop="endStation" label=""></el-table-column>
        <el-table-column prop="customerName" label=""></el-table-column>
        <el-table-column prop="consignee" label=""></el-table-column>
        <el-table-column prop="consigneeAddress" label=""></el-table-column>
        <el-table-column prop="payment" label=""></el-table-column>
        <el-table-column prop="goods" label=""></el-table-column>
        <el-table-column prop="num" label=""></el-table-column>
        <el-table-column prop="quantity" label=""></el-table-column>
        <el-table-column prop="collection" label=""></el-table-column>
        <el-table-column prop="unit" label=""></el-table-column>
        <el-table-column prop="insured" label=""></el-table-column>
        <el-table-column prop="otherPrice" label=""></el-table-column>
        <el-table-column prop="discount" label=""></el-table-column>
        <el-table-column prop="transferFee" label=""></el-table-column>
        <el-table-column prop="deliveryFee" label=""></el-table-column>
        <el-table-column prop="sum" label=""></el-table-column>
        <el-table-column prop="carNumber" label=""></el-table-column>
        <el-table-column prop="driver" label=""></el-table-column>
        <el-table-column prop="register" label=""></el-table-column>
        <el-table-column prop="state" label="">-->

        <!-- <el-table-column prop="customerAddress" label=""></el-table-column> -->
        <!-- <el-table-column prop="price" label=""></el-table-column> -->
        <!-- <el-table-column prop="money" label=""></el-table-column> -->
        <!-- <el-table-column prop="insuredSum" label=""></el-table-column> -->
        <!-- <el-table-column prop="insuredRatio" label=""></el-table-column> -->

        <!-- <el-table-column prop="state" label="">
          <template slot-scope="scope">
            <div>
              <span>{{scope.row.state == 0 ? '' : ''}}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="remark" label=""></el-table-column>-->
        <el-table-column label="" fixed="right" width="210">
          <template slot-scope="scope">
            <el-button type="primary" @click="go(scope.row.kddh)"></el-button>
            <el-dropdown
              split-button
              type="primary"
              @click="print('integration','PrintReport',scope.row.kddh)"
              style="margin-left:10px"
            >
              
              <el-dropdown-menu slot="dropdown" v-if="!$store.state.mobile">
                <el-dropdown-item
                  @click.native="print('integration','ShowReport',scope.row.kddh)"
                ></el-dropdown-item>
                <el-dropdown-item
                  @click.native="print('integration','DesignReport',scope.row.kddh)"
                  v-if="$store.state.account && $store.state.account.administrator"
                ></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>
      </el-table>
      <el-table
        :data="tableData2"
        border
        height="250"
        class="card"
        key=""
        v-if="dataType === ''"
        :row-class-name="$tableRowClassName"
      >
        <el-table-column prop="date" label="" width="180"></el-table-column>
        <el-table-column prop="kddh" label="" width="180"></el-table-column>
        <el-table-column prop="startStation" label="" width="180"></el-table-column>
        <el-table-column prop="endStation" label="" width="180"></el-table-column>
        <el-table-column prop="carNumber" label=""></el-table-column>
        <el-table-column prop="creator" label="" width="180"></el-table-column>
        <el-table-column label="" width="280" fixed="right">
          <template slot-scope="scope">
            <el-button type="primary" @click="go2(scope.$index)"></el-button>
            <el-dropdown
              split-button
              type="primary"
              @click="print('integration','PrintReport',scope.row.kddh)"
              style="margin:0 10px"
            >
              
              <el-dropdown-menu slot="dropdown" v-if="!$store.state.mobile">
                <el-dropdown-item
                  @click.native="print('integration','ShowReport',scope.row.kddh)"
                ></el-dropdown-item>
                <el-dropdown-item
                  @click.native="print('integration','DesignReport',scope.row.kddh)"
                  v-if="$store.state.account && $store.state.account.administrator"
                ></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <el-button
              type="danger"
              @click="remove(scope.$index)"
              v-if="permission(['all','integrationEdit'])"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-pagination
      background
      layout="prev, pager, next"
      style="margin-top:10px"
      :total="total"
      :current-page.sync="searchMsg.i"
      :page-size="20"
      @current-change="currentChange"
    ></el-pagination>
    <transition>
      <integration
        position="absolute"
        v-if="show"
        :kddh="kddh"
        :propMsg="msg"
        :dataType="dataType"
        :propDataList="dataList"
        @change="changeTransport"
        @cancel="show = false"
      ></integration>
    </transition>
  </div>
</template>

<script type="text/javascript">
import integration from '@/views/bill/integration.vue'
export default {
  created() {
    this.refresh(true)
    const checkList = JSON.parse(localStorage.getItem('inteTableC'))
    if (checkList && checkList.length > 0) {
      this.checkList = checkList
    } else {
      this.checkList = this.formatTable.map(item => item.name)
    }
  },
  data() {
    return {
      tablecolumns: [],
      checkList: [],
      dialogVisible: false,
      endStationArr: [],
      startStationArr: [],
      dh: '',
      dataType: '',
      total: 0,
      searchMsg: {
        startDate: this.$moment(
          new Date(new Date().getFullYear(), new Date().getMonth(), 1)
        ),
        endDate: this.$moment(new Date()),
        startStation: '',
        endStation: '',
        customerName: '',
        consignee: '',
        payment: '',
        state: '',
        goods: '',
        dh: '',
        i: 1
      },
      tableData: [],
      tableData2: [],
      msg: {},
      dataList: [],
      show: false,
      showI: 0,
      selectData: [],
      format: {
        kddh: '', // ----
        dh: '',
        date: '',
        startStation: '',
        endStation: '',
        customerName: '',
        // customerAddress: '',
        consignee: '',
        consigneeAddress: '',
        payment: '',
        goods: '',
        num: '',
        quantity: '',
        collection: '',
        unit: '',
        // price: '',
        // money: '',
        insured: '',
        // insuredSum: '',
        // insuredRatio: '',
        otherPrice: '',
        discount: '',
        transferFee: '',
        deliveryFee: '',
        carNumber: '', // ----
        driver: '', // ----
        register: '', // ----
        sum: '',
        state: '',
        remark: ''
      },
      formatTable: [
        { type: 'kddh', name: '', i: 0 }, // ----
        { type: 'dh', name: '', i: 1 },
        { type: 'date', name: '', i: 2 },
        { type: 'startStation', name: '', i: 3 },
        { type: 'endStation', name: '', i: 4 },
        { type: 'customerName', name: '', i: 5 },
        // customerAddress',name: '',i:0},
        { type: 'consignee', name: '', i: 6 },
        { type: 'consigneeAddress', name: '', i: 7 },
        { type: 'payment', name: '', i: 8 },
        { type: 'goods', name: '', i: 9 },
        { type: 'num', name: '', i: 10 },
        { type: 'quantity', name: '', i: 11 },
        { type: 'collection', name: '', i: 12 },
        { type: 'unit', name: '', i: 13 },
        // price',name: '',i:0},
        // money',name: '',i:0},
        { type: 'insured', name: '', i: 14 },
        // insuredSum',name: '',i:0},
        // insuredRatio',name: '',i:0},
        { type: 'otherPrice', name: '', i: 15 },
        { type: 'discount', name: '', i: 16 },
        { type: 'transferFee', name: '', i: 17 },
        { type: 'deliveryFee', name: '', i: 18 },
        { type: 'carNumber', name: '', i: 19 }, // ----
        { type: 'driver', name: '', i: 20 }, // ----
        { type: 'register', name: '', i: 21 }, // ----
        { type: 'sum', name: '', i: 22 },
        { type: 'state', name: '', i: 23 },
        { type: 'remark', name: '', i: 24 }
      ]
    }
  },
  methods: {
    print(type, method, kddh) {
      this.$postPrint({
        type,
        data: { kddh, type, method },
        method,
        that: this
      })
    },
    checkListH(val) {
      this.tablecolumns = val.map(item => {
        return this.formatTable.filter(it => {
          if (item === it.name) {
            return true
          }
        })[0]
      })
      this.tablecolumns.sort((a, b) => {
        return a.i - b.i
      })
      localStorage.setItem('inteTableC', JSON.stringify(val))
      this.$nextTick(() => {
        this.$refs.multipleTable.doLayout()
      })
    },
    remove(i) {
      if (this.tableData2[i].dataList.some(item => item.state === 1)) {
        this.$message.error('')
        return
      }
      this.$confirm('').then(_ => {
        const jlidArr = this.tableData2[i].dataList.map(item => {
          return item.jlid
        })
        this.$req('/integration/delete', {
          kddh: this.tableData2[i].kddh,
          jlidArr
        }).then(res => {
          if (res.status === 1) {
            this.tableData2.splice(i, 1)
            this.$emit('refresh', { name: 'transportStatistics' })
            this.$message({
              message: '',
              type: 'success'
            })
          }
        })
      })
    },
    refresh(flag) {
      if (this.dataType === '') {
        this.get(flag)
      } else if (this.dataType === '') {
        this.get2(flag)
      }
    },
    changeType() {
      if (this.dataType === '') {
        this.dataType = ''
      } else if (this.dataType === '') {
        this.dataType = ''
      }
    },
    goInventory() {
      if (this.selectData.length === 0) {
        return this.$message.error('')
      }
      if (
        this.selectData.some((item, i) => {
          // eslint-disable-next-line
          if (item.state == 1) {
            this.$message.error(
              `${i + 1}${item.dh}`
            )
            return true
          }
        })
      ) {
        return
      }
      this.$emit('editable', 'inventory', {
        name: 'inventory',
        msg: this.selectData
      })
    },
    changeTransport(e) {
      this.$set(this.tableData, this.showI, e)
    },
    handleSelectionChange(e) {
      this.selectData = e
    },
    go(kddh) {
      this.show = true
      this.kddh = kddh
    },
    go2(i) {
      this.showI = i
      this.show = true
      const obj = {}
      Object.keys(this.tableData2[i]).forEach(key => {
        if (key !== 'dataList') {
          obj[key] = this.tableData2[i][key]
        }
      })
      this.msg = obj
      this.dataList = this.tableData2[i].dataList
    },
    currentChange(e) {
      this.refresh()
    },
    get(flag) {
      if (flag) {
        this.searchMsg.i = 1
      }
      this.$req('/integration/join', this.searchMsg).then(res => {
        if (res && res.status === 1) {
          this.tableData = res.res
          this.total = res.total
        } else {
          this.tableData = []
          this.$message({
            message: '',
            type: 'error'
          })
        }
      })
    },
    get2(flag) {
      if (flag) {
        this.searchMsg.i = 1
      }
      this.$req('/integration', this.searchMsg).then(res => {
        if (res && res.status === 1) {
          this.tableData2 = res.res
          this.total = res.total
        } else {
          this.tableData = []
          this.$message({
            message: '',
            type: 'error'
          })
        }
      })
    }
  },
  watch: {
    checkList: {
      handler: 'checkListH',
      deep: true
    },
    dataType(val) {
      if (val === '') {
        this.get(true)
      } else {
        this.get2(true)
      }
    }
  },
  components: {
    integration
  }
}
</script>

<style lang="scss" scoped>
</style>
