<template>
  <div class="page">
    <header></header>
    <div class="pagecontent">
      <div class="saveForm" style="height:90%;width:100%;">
        <el-form
          ref="form"
          label-position="left"
          class="card"
          style="display:flex;height:98%;width:100%;padding-top:20px"
          :label-width="$store.state.mobile ? '' : ''"
        >
          <el-form-item label="" style="width:48%">
            <template>
              <el-input v-model="msg.corporateName" placeholder=""></el-input>
            </template>
          </el-form-item>
          <el-form-item label="" style="width:48%">
            <template>
              <el-input v-model="msg.englishName" placeholder=""></el-input>
            </template>
          </el-form-item>
          <!--eslint-disable-->
          <el-form-item label="  " style="width:48%">
            <template>
              <el-input v-model="msg.contacts" placeholder=""></el-input>
            </template>
          </el-form-item>
          <el-form-item label="" style="width:48%">
            <template>
              <el-input v-model="msg.phone" placeholder=""></el-input>
            </template>
          </el-form-item>
          <el-form-item label="" style="width:48%">
            <template>
              <el-input v-model="msg.mobilePhone" placeholder=""></el-input>
            </template>
          </el-form-item>
          <el-form-item label="" style="width:48%">
            <template>
              <el-input v-model="msg.fax" placeholder=""></el-input>
            </template>
          </el-form-item>
          <el-form-item label="" style="width:48%">
            <template>
              <el-input v-model="msg.website" placeholder=""></el-input>
            </template>
          </el-form-item>
          <el-form-item label="" style="width:48%">
            <template>
              <el-input v-model="msg.mailbox" placeholder=""></el-input>
            </template>
          </el-form-item>
          <el-form-item label="" style="width:48%">
            <template>
              <el-input v-model="msg.address" placeholder=""></el-input>
            </template>
          </el-form-item>
          <el-form-item label="" style="width:48%">
            <template>
              <el-input v-model="msg.tip" placeholder="" type="textarea"></el-input>
            </template>
          </el-form-item>
        </el-form>
      </div>
      <div class="bottomBtn-right">
        <el-button type="success" @click="save" v-if="permission(['all'])"></el-button>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
export default {
  created() {
    this.get()
  },
  data() {
    return {
      msg: {
        corporateName: '',
        englishName: '',
        contacts: '',
        phone: '',
        mobilePhone: '',
        fax: '',
        website: '',
        mailbox: '',
        address: '',
        tip: ''
      }
    }
  },
  methods: {
    save() {
        this.$req('/base/update', { ...this.msg }).then(res => {
          if (res.status === 1) {
            this.$message({
              message: '',
              type: 'success'
            })
          } else {
            this.$message({
              message: '',
              type: 'error'
            })
          }
        })
    },
    get() {
      this.$req('/base', {}, { method: 'get' }).then(res => {
        if (res && res.res && res.res.length > 0) {
          this.msg = res.res[0]
        }
      })
    }
  },
  watch: {

  }
}
</script>

<style lang="scss">
.el-textarea__inner {
  height: 100% !important;
}
</style>
