<template>
  <div class="page">
    <!-- <header></header> -->
    <div class="pagecontent">
      <div class="saveForm">
        <el-form
          ref="form"
          label-position="left"
          class="card"
          :label-width="$store.state.mobile ? '' : ''"
        >
          <!--eslint-disable-->
          <el-form-item label="   ">
            <el-date-picker
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
              v-model="searchMsg.startDate"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="   ">
            <el-date-picker
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
              v-model="searchMsg.endDate"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <!-- <el-autocomplete
              :highlight-first-item="true"
              ref="customerName"
              placeholder=""
              multiple
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.startStation"
              :fetch-suggestions="(str,cb)=>{$querySearchAsync(str,cb,'startStation','startStation')}"
            ></el-autocomplete>-->
            <el-select
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.startStation"
              multiple
              filterable
              reserve-keyword
              placeholder=""
              @focus="$remoteMethod('','startStation','startStation',(arr)=>{startStationArr = arr})"
            >
              <el-option
                v-for="item in startStationArr"
                :key="item.startStation"
                :label="item.startStation"
                :value="item.startStation"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <!-- <el-autocomplete
              :highlight-first-item="true"
              ref="storeName"
              placeholder=""
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.endStation"
              :fetch-suggestions="(str,cb)=>{$querySearchAsync(str,cb,'endStation','endStation')}"
            ></el-autocomplete>-->
            <el-select
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.endStation"
              multiple
              filterable
              reserve-keyword
              placeholder=""
              @focus="$remoteMethod('','endStation','endStation',(arr)=>{endStationArr = arr})"
            >
              <el-option
                v-for="item in endStationArr"
                :key="item.endStation"
                :label="item.endStation"
                :value="item.endStation"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-autocomplete
              :highlight-first-item="true"
              ref="storeName"
              placeholder=""
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.customerName"
              :fetch-suggestions="(str,cb)=>{$querySearchAsync(str,cb,'customer','customerName')}"
            ></el-autocomplete>
          </el-form-item>
          <el-form-item label="">
            <el-autocomplete
              :highlight-first-item="true"
              ref="storeName"
              placeholder=""
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.consignee"
              :fetch-suggestions="(str,cb)=>{$querySearchAsync(str,cb,'consignee','consignee')}"
            ></el-autocomplete>
          </el-form-item>
          <el-form-item label="   ">
            <el-select
              v-model="searchMsg.payment"
              placeholder=""
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
            >
              <el-option
                v-for="item in [{label:'',value:''},{label:'',value:''},{label:'',value:''},{label:'',value:''}]"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="   ">
            <el-select
              v-model="searchMsg.state"
              placeholder=""
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
            >
              <el-option
                v-for="item in [{label:'',value:''},{label:'',value:0},{label:'',value:1}]"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-autocomplete
              :highlight-first-item="true"
              ref="storeName"
              placeholder=""
              :style="{width:$store.state.mobile ? '150px' : '200px'}"
              v-model="searchMsg.goods"
              :fetch-suggestions="(str,cb)=>{$querySearchAsync(str,cb,'goods','goods')}"
            ></el-autocomplete>
          </el-form-item>
          <el-form-item style="width:50%">
            <div style="width:100%;display:flex">
              <el-button type="primary" @click="refresh(true)"></el-button>
              <el-button type="warning" @click="changeType">{{dataType}}</el-button>
              <el-button
                type="success"
                @click="goIntegration"
                v-if="dataType==='' && permission(['all','integrationEdit'])"
              ></el-button>
              <exportExcel
                style="margin-left:10px"
                :format="format"
                :msg="selectData"
                v-if="dataType==='' && !$store.state.mobile"
              ></exportExcel>
              <el-button
                type="info"
                @click="dialogVisible = true"
                style="margin-left:10px"
                v-if="dataType===''"
              ></el-button>
              <el-dialog title="" :visible.sync="dialogVisible" width="30%" append-to-body>
                <el-checkbox-group v-model="checkList">
                  <el-checkbox
                    :label="item.name"
                    v-for="item in formatTable"
                    :key="item.type"
                    v-model="item.flag"
                  ></el-checkbox>
                </el-checkbox-group>
              </el-dialog>
            </div>
          </el-form-item>
        </el-form>
      </div>
      <el-table
        :data="tableData"
        ref="multipleTable"
        border
        height="250"
        class="card"
        key=""
        v-if="dataType === ''"
        @selection-change="handleSelectionChange"
        :row-class-name="$tableRowClassName"
        :cell-class-name="$tableCellClassName"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
          :prop="item.type"
          :label="item.name"
          v-for="item in tablecolumns"
          :key="item.type"
          :width="item.type === 'date' && 100"
        >
          <template slot-scope="scope">
            <div v-if="item.type === 'state'">
              <span>{{scope.row.state == 0 ? '' : ''}}</span>
            </div>
            <div v-else>
              <span>{{scope.row[item.type]}}</span>
            </div>
          </template>
        </el-table-column>
        <!--  <el-table-column prop="date" label="" width="100"></el-table-column>
        <el-table-column prop="startStation" label=""></el-table-column>
        <el-table-column prop="endStation" label=""></el-table-column>
        <el-table-column prop="customerName" label=""></el-table-column>
        <el-table-column prop="customerAddress" label=""></el-table-column>
        <el-table-column prop="consignee" label=""></el-table-column>
        <el-table-column prop="consigneeAddress" label=""></el-table-column>
        <el-table-column prop="payment" label=""></el-table-column>
        <el-table-column prop="goods" label=""></el-table-column>
        <el-table-column prop="num" label=""></el-table-column>
        <el-table-column prop="quantity" label=""></el-table-column>
        <el-table-column prop="collection" label=""></el-table-column>
        <el-table-column prop="unit" label=""></el-table-column>
        <el-table-column prop="price" label=""></el-table-column>
        <el-table-column prop="money" label=""></el-table-column>
        <el-table-column prop="insured" label=""></el-table-column>
        <el-table-column prop="insuredSum" label=""></el-table-column>
        <el-table-column prop="insuredRatio" label=""></el-table-column>
        <el-table-column prop="otherPrice" label=""></el-table-column>
        <el-table-column prop="discount" label=""></el-table-column>
        <el-table-column prop="transferFee" label=""></el-table-column>
        <el-table-column prop="deliveryFee" label=""></el-table-column>
        <el-table-column prop="sum" label=""></el-table-column>-->
        <!-- <el-table-column prop="state" label="">
          <template slot-scope="scope">
            <div>
              <span>{{scope.row.state == 0 ? '' : ''}}</span>
            </div>
          </template>
        </el-table-column>-->
        <!-- <el-table-column prop="remark" label=""></el-table-column> -->
        <el-table-column label="" fixed="right" width="520">
          <template slot-scope="scope">
            <el-button type="primary" @click="go(scope.row.id)"></el-button>
            <el-dropdown
              split-button
              type="primary"
              @click="print('contract','PrintReport',scope.row.id)"
              style="margin-left:10px"
            >
              
              <el-dropdown-menu slot="dropdown" v-if="!$store.state.mobile">
                <el-dropdown-item @click.native="print('contract','ShowReport',scope.row.id)"></el-dropdown-item>
                <el-dropdown-item
                  @click.native="print('contract','DesignReport',scope.row.id)"
                  v-if="$store.state.account && $store.state.account.administrator"
                ></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <el-dropdown
              split-button
              type="primary"
              @click="print('qrcode','PrintReport',scope.row.id)"
              style="margin-left:10px"
            >
              
              <el-dropdown-menu slot="dropdown" v-if="!$store.state.mobile">
                <el-dropdown-item @click.native="print('qrcode','ShowReport',scope.row.id)"></el-dropdown-item>
                <el-dropdown-item
                  @click.native="print('qrcode','DesignReport',scope.row.id)"
                  v-if="$store.state.account && $store.state.account.administrator"
                ></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <el-dropdown
              split-button
              type="primary"
              @click="print('ticket','PrintReport',scope.row.id)"
              style="margin-left:10px"
            >
              
              <el-dropdown-menu slot="dropdown" v-if="!$store.state.mobile">
                <el-dropdown-item @click.native="print('ticket','ShowReport',scope.row.id)"></el-dropdown-item>
                <el-dropdown-item
                  @click.native="print('ticket','DesignReport',scope.row.id)"
                  v-if="$store.state.account && $store.state.account.administrator"
                ></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>
      </el-table>
      <el-table
        :data="tableData2"
        border
        key=""
        height="250"
        class="card"
        v-if="dataType === ''"
        :row-class-name="$tableRowClassName"
      >
        <el-table-column prop="date" label="" width="100"></el-table-column>
        <el-table-column prop="dh" label=""></el-table-column>
        <el-table-column prop="startStation" label=""></el-table-column>
        <el-table-column prop="endStation" label=""></el-table-column>
        <el-table-column prop="customerName" label=""></el-table-column>
        <el-table-column prop="consignee" label=""></el-table-column>
        <el-table-column prop="lsh" label=""></el-table-column>
        <el-table-column prop="creator" label=""></el-table-column>
        <el-table-column label="" fixed="right" width="590">
          <template slot-scope="scope">
            <el-button type="primary" @click="go2(scope.$index)"></el-button>
            <el-dropdown
              split-button
              type="primary"
              @click="print('contract','PrintReport',scope.row.id)"
              style="margin-left:5px"
            >
              
              <el-dropdown-menu slot="dropdown" v-if="!$store.state.mobile">
                <el-dropdown-item @click.native="print('contract','ShowReport',scope.row.id)"></el-dropdown-item>
                <el-dropdown-item
                  @click.native="print('contract','DesignReport',scope.row.id)"
                  v-if="$store.state.account && $store.state.account.administrator"
                ></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <el-dropdown
              split-button
              type="primary"
              @click="print('qrcode','PrintReport',scope.row.id)"
              style="margin-left: 5px"
            >
              
              <el-dropdown-menu slot="dropdown" v-if="!$store.state.mobile">
                <el-dropdown-item @click.native="print('qrcode','ShowReport',scope.row.id)"></el-dropdown-item>
                <el-dropdown-item
                  @click.native="print('qrcode','DesignReport',scope.row.id)"
                  v-if="$store.state.account && $store.state.account.administrator"
                ></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <el-dropdown
              split-button
              type="primary"
              @click="print('ticket','PrintReport',scope.row.id)"
              style="margin-left:10px"
            >
              
              <el-dropdown-menu slot="dropdown" v-if="!$store.state.mobile">
                <el-dropdown-item @click.native="print('ticket','ShowReport',scope.row.id)"></el-dropdown-item>
                <el-dropdown-item
                  @click.native="print('ticket','DesignReport',scope.row.id)"
                  v-if="$store.state.account && $store.state.account.administrator"
                ></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <el-button
              style="margin-left: 5px"
              type="danger"
              @click="remove(scope.$index)"
              v-if="permission(['all','transportEdit'])"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-pagination
      background
      layout="prev, pager, next"
      style="margin-top:10px"
      :total="total"
      :current-page.sync="searchMsg.i"
      :page-size="20"
      @current-change="currentChange"
    ></el-pagination>
    <transition>
      <transport
        position="absolute"
        v-if="show"
        :id2="id2"
        :propMsg="msg"
        :dataType="dataType"
        :propDataList="dataList"
        @change="changeTransport"
        @cancel="show = false"
      ></transport>
    </transition>
  </div>
</template>

<script type="text/javascript">
import transport from '@/views/bill/transport.vue'
export default {
  created() {
    this.get(true)
    const checkList = JSON.parse(localStorage.getItem('tranTableC'))
    if (checkList && checkList.length > 0) {
      this.checkList = checkList
    } else {
      this.checkList = this.formatTable.map(item => item.name)
    }
  },
  mounted() {
    this.$refs.multipleTable.doLayout()
  },
  data() {
    return {
      tablecolumns: [],
      checkList: [],
      dialogVisible: false,
      endStationArr: [],
      startStationArr: [],
      id2: '',
      dataType: '',
      total: 0,
      searchMsg: {
        startDate: this.$moment(
          new Date(new Date().getFullYear(), new Date().getMonth(), 1)
        ),
        endDate: this.$moment(new Date()),
        startStation: '',
        endStation: '',
        customerName: '',
        consignee: '',
        payment: '',
        state: '',
        goods: '',
        i: 1
      },
      tableData: [],
      tableData2: [],
      msg: {},
      dataList: [],
      show: false,
      showI: 0,
      selectData: [],
      format: {
        dh: '',
        date: '',
        startStation: '',
        endStation: '',
        customerName: '',
        customerAddress: '',
        consignee: '',
        consigneeAddress: '',
        payment: '',
        goods: '',
        num: '',
        quantity: '',
        collection: '',
        unit: '',
        price: '',
        money: '',
        insured: '',
        insuredSum: '',
        insuredRatio: '',
        otherPrice: '',
        discount: '',
        transferFee: '',
        deliveryFee: '',
        sum: '',
        state: '',
        remark: ''
      },
      formatTable: [
        { type: 'dh', name: '', i: 0 },
        { type: 'date', name: '', i: 1 },
        { type: 'startStation', name: '', i: 2 },
        { type: 'endStation', name: '', i: 3 },
        { type: 'customerName', name: '', i: 4 },
        { type: 'customerAddress', name: '', i: 5 },
        { type: 'consignee', name: '', i: 6 },
        { type: 'consigneeAddress', name: '', i: 7 },
        { type: 'payment', name: '', i: 8 },
        { type: 'goods', name: '', i: 9 },
        { type: 'num', name: '', i: 10 },
        { type: 'quantity', name: '', i: 11 },
        { type: 'collection', name: '', i: 12 },
        { type: 'unit', name: '', i: 13 },
        { type: 'price', name: '', i: 14 },
        { type: 'money', name: '', i: 15 },
        { type: 'insured', name: '', i: 16 },
        { type: 'insuredSum', name: '', i: 17 },
        { type: 'insuredRatio', name: '', i: 18 },
        { type: 'otherPrice', name: '', i: 19 },
        { type: 'discount', name: '', i: 20 },
        { type: 'transferFee', name: '', i: 21 },
        { type: 'deliveryFee', name: '', i: 22 },
        { type: 'sum', name: '', i: 23 },
        { type: 'state', name: '', i: 24 },
        { type: 'remark', name: '', i: 25 }
      ]
    }
  },
  watch: {
    checkList: {
      handler: 'checkListH',
      deep: true
    },
    dataType(val) {
      if (val === '') {
        this.get(true)
      } else {
        this.get2(true)
      }
    }
  },
  methods: {
    print(type, method, id) {
      this.$postPrint({
        type,
        data: { id, type, method },
        method,
        that: this
      })
    },
    checkListH(val) {
      this.tablecolumns = val.map(item => {
        return this.formatTable.filter(it => {
          if (item === it.name) {
            return true
          }
        })[0]
      })
      this.tablecolumns.sort((a, b) => {
        return a.i - b.i
      })
      localStorage.setItem('tranTableC', JSON.stringify(val))
      this.$nextTick(() => {
        this.$refs.multipleTable.doLayout()
      })
    },
    remove(i) {
      if (this.tableData2[i].dataList.some(item => item.state === 1)) {
        this.$message.error('')
        return
      }
      this.$confirm('').then(_ => {
        this.$req('/transport/delete', { id: this.tableData2[i].id }).then(
          res => {
            if (res.status === 1) {
              this.tableData2.splice(i, 1)
              this.$message({
                message: '',
                type: 'success'
              })
            }
          }
        )
      })
    },
    refresh(flag) {
      if (this.dataType === '') {
        this.get(flag)
      } else if (this.dataType === '') {
        this.get2(flag)
      }
    },
    changeType() {
      if (this.dataType === '') {
        this.dataType = ''
      } else if (this.dataType === '') {
        this.dataType = ''
      }
    },
    goIntegration() {
      if (this.selectData.length === 0) {
        return this.$message.error('')
      }
      if (
        this.selectData.some((item, i) => {
          // eslint-disable-next-line
          if (item.state == 1) {
            this.$message.error(
              `${i + 1}${item.dh}`
            )
            return true
          }
        })
      ) {
        return
      }
      this.$emit('editable', 'integration', {
        name: 'integration',
        msg: this.selectData
      })
    },
    changeTransport(e) {
      this.$set(this.tableData, this.showI, e)
    },
    handleSelectionChange(e) {
      /* const flag = true
      if (flag) {
        this.$refs.multipleTable.toggleRowSelection(this.tableData[3])
        return
      } */
      this.selectData = e
    },
    go(id) {
      this.show = true
      this.id2 = id
    },
    go2(i) {
      this.showI = i
      this.show = true
      const obj = {}
      Object.keys(this.tableData2[i]).forEach(key => {
        if (key !== 'dataList') {
          obj[key] = this.tableData2[i][key]
        }
      })
      this.msg = obj
      this.dataList = this.tableData2[i].dataList
    },
    currentChange(e) {
      this.refresh()
    },
    get(flag) {
      if (flag) {
        this.searchMsg.i = 1
      }
      this.$req('/transport/join', this.searchMsg).then(res => {
        if (res && res.status === 1) {
          this.tableData = res.res
          this.total = res.total
        } else {
          this.tableData = []
          this.$message({
            message: '',
            type: 'error'
          })
        }
      })
    },
    get2(flag) {
      if (flag) {
        this.searchMsg.i = 1
      }
      this.$req('/transport', this.searchMsg).then(res => {
        if (res && res.status === 1) {
          this.tableData2 = res.res
          this.total = res.total
        } else {
          this.tableData = []
          this.$message({
            message: '',
            type: 'error'
          })
        }
      })
    }
  },
  computed: {},
  components: {
    transport
  }
}
</script>
