<template>
  <!-- eslint-disable -->
  <div class="Record">
    <div class="searchTop">
      <div class="topBox">
        <el-form
          ref="form"
          label-position="left"
          class="card2"
          style="flex-wrap:nowrap;width:560px"
          :label-width="$store.state.mobile ? '' : ''"
        >
          <el-form-item label="">
            <el-input placeholder="" v-model="searchMsg.customerName" clearable></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input placeholder="" v-model="searchMsg.telephone" clearable></el-input>
          </el-form-item>
        </el-form>
        <el-button type="primary" icon="el-icon-search" style="margin-left:20px" @click="search"></el-button>
        <el-button
          type="success"
          icon="el-icon-circle-plus-outline"
          style="margin-left:20px"
          v-if="permission(['all','customerEdit'])"
          @click="add"
        ></el-button>
        <exportExcel
          style="margin-left:10px"
          :format="format"
          :msg="tableData"
          v-if="!$store.state.mobile"
        ></exportExcel>
      </div>
      <!-- <header></header> -->
    </div>
    <div class="elTable">
      <el-table :data="tableData" height="250">
        <el-table-column prop="customerName" label=""></el-table-column>
        <el-table-column prop="freight" label=""></el-table-column>
        <el-table-column prop="address" label=""></el-table-column>
        <el-table-column prop="telephone" label="" width="110"></el-table-column>
        <el-table-column prop="phone" label=""></el-table-column>
        <el-table-column prop="liaison" label=""></el-table-column>
        <el-table-column prop="fax" label=""></el-table-column>
        <el-table-column prop="simpleCode" label=""></el-table-column>
        <el-table-column prop="remark" label=""></el-table-column>
        <el-table-column
          label=""
          width="150"
          v-if="permission(['all','customerEdit'])"
          fixed="right"
        >
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="change(scope.$index)"></el-button>
            <el-button size="mini" type="danger" @click="remove(scope.$index)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      layout="prev, pager, next"
      style="margin-top:10px"
      :total="total"
      :current-page.sync="searchMsg.i"
      :page-size="20"
      @current-change="currentChange"
    ></el-pagination>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%" close-on-click-modal>
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="confirmDel"> </el-button>
      </span>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="dialogFormVisible"
      :close-on-click-modal="false"
      :show-close="true"
      :destroy-on-close="true"
    >
      <div style="height:300px;width:100%;display:flex;flex-direction:column;align-items:center">
        <el-form
          label-position="right"
          :label-width="$store.state.mobile ? '70px' : '70px'"
          :model="form"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="" prop="customerName">
            <el-input v-model.trim="form.customerName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="freight">
            <el-input v-model.trim="form.freight" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input v-model.trim="form.address" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="telephone">
            <el-input v-model.number="form.telephone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input v-model.number="form.phone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="liaison">
            <el-input v-model.trim="form.liaison" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model.trim="form.fax" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="simpleCode">
            <el-input v-model.trim="form.simpleCode" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model.trim="form.remark" placeholder="" type="textarea"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="confirm"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/javascript">
export default {
  created() {
    this.initFormData()
    this.get()
  },
  data() {
    return {
      index: 0,
      format: {
        customerName: '',
        freight: '',
        address: '',
        telephone: '',
        phone: '',
        liaison: '',
        fax: '',
        simpleCode: '',
        remark: ''
      },
      Initform: {
        id: '',
        customerName: '',
        freight: '',
        address: '',
        telephone: '',
        phone: '',
        liaison: '',
        fax: '',
        simpleCode: '',
        remark: ''
      },
      form: {},
      oldForm: {},
      type: 'add',
      total: 1,
      searchMsg: {
        customerName: '',
        telephone: '',
        i: 1
      },
      tableData: [],
      dialogVisible: false,
      dialogFormVisible: false,
      rules: {
        customerName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address: [
          { required: true, message: '', trigger: 'blur' }
        ],
        freight: [{ required: true, message: '', trigger: 'blur' }],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', message: '', trigger: 'blur' }
        ],
        telephone: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', message: '', trigger: 'blur' }
        ],
        liaison: [{ required: true, message: '', trigger: 'blur' }],
        simpleCode: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  watch: {
    'form.customerName'(value) {
      this.form.simpleCode = this.py.getCamelChars(value)
    }
  },
  methods: {
    get() {
      this.$req('customer', this.searchMsg, {
        method: 'get'
      }).then(data => {
        this.tableData = data.res
        this.total = data.total
      })
    },
    search() {
      this.searchMsg.i = 1
      this.get()
    },
    add() {
      this.type = 'add'
      this.initFormData()
      this.dialogFormVisible = true
    },
    change(i) {
      this.index = i
      this.type = 'change'
      this.tableData[i].phone = parseInt(this.tableData[i].phone)
      this.tableData[i].telephone = parseInt(this.tableData[i].telephone)
      this.form = this.copy(this.tableData[i])
      this.oldForm = this.copy(this.form)
      this.dialogFormVisible = true
    },
    remove(i) {
      this.index = i
      this.dialogVisible = true
    },
    confirm() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          if (this.form.telephone.toString().length !== 11) {
            return this.$message.error('11')
          }
          if (this.type === 'add') {
            this.$req('customer/add', this.form).then(data => {
              if (data.status === 1) {
                this.$message({
                  message: '',
                  type: 'success'
                })
                this.tableData.unshift(data.res)
                this.initFormData()
              } else if (data.status === 0) {
                this.$message.error('')
              }
            })
          } else {
            this.$req('customer/update', this.form).then(data => {
              if (data.status === 1) {
                this.$message({
                  message: '',
                  type: 'success'
                })
                this.tableData.splice(this.index, 1, this.form)
              } else if (data.status === 0) {
                this.$message.error('')
              }
            })
          }
        }
      })
    },
    cancel() {
      this.dialogFormVisible = false
    },
    confirmDel() {
      this.$req('customer/delete', {
        id: this.tableData[this.index].id
      }).then(data => {
        if (data.status === 1) {
          this.$message({
            message: '',
            type: 'success'
          })
          this.tableData.splice(this.index, 1)
        } else if (data.status === 0) {
          this.$message.error('')
        }
        this.dialogVisible = false
      })
    },
    currentChange(i) {
      this.searchMsg.i = i
      this.get()
    },
    initFormData() {
      this.form = this.copy(this.Initform)
    }
  }
}
</script>
