<template>
  <!-- eslint-disable -->
  <div class="Record">
    <div class="searchTop">
      <div class="topBox" style="flex-wrap:wrap;height:80px;justify-content:flex-start">
        <el-form
          ref="form"
          label-position="right"
          class="card2"
          style="flex-wrap:nowrap;width:100%"
          :label-width="$store.state.mobile ? '80px' : '80px'"
        >
          <el-form-item label="">
            <el-input placeholder="" v-model="searchMsg.carType" clearable></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input placeholder="" v-model="searchMsg.carNumber" clearable></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input placeholder="" v-model="searchMsg.register" clearable></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input placeholder="" v-model="searchMsg.driver" clearable></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input placeholder="" v-model="searchMsg.phone" clearable></el-input>
          </el-form-item>
        </el-form>
        <div style="display:flex;height:38px">
          <el-button
            type="primary"
            icon="el-icon-search"
            style="margin-left:20px"
            @click="search"
          ></el-button>
          <el-button
            type="success"
            icon="el-icon-circle-plus-outline"
            style="margin-left:20px"
            v-if="permission(['all','carEdit'])"
            @click="add"
          ></el-button>
          <exportExcel
            style="margin-left:10px"
            :format="format"
            :msg="tableData"
            v-if="!$store.state.mobile"
          ></exportExcel>
        </div>
      </div>
      <!-- <header></header> -->
    </div>
    <div class="elTable">
      <el-table :data="tableData" height="250">
        <el-table-column prop="carType" label=""></el-table-column>
        <el-table-column prop="carNumber" label=""></el-table-column>
        <el-table-column prop="register" label=""></el-table-column>
        <el-table-column prop="driver" label=""></el-table-column>
        <el-table-column prop="phone" label=""></el-table-column>
        <el-table-column prop="license" label=""></el-table-column>
        <el-table-column prop="company" label=""></el-table-column>
        <el-table-column prop="remark" label=""></el-table-column>
        <el-table-column label="" width="150" v-if="permission(['all','carEdit'])" fixed="right">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="change(scope.$index)"></el-button>
            <el-button size="mini" type="danger" @click="remove(scope.$index)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      layout="prev, pager, next"
      style="margin-top:10px"
      :total="total"
      :current-page.sync="searchMsg.i"
      :page-size="20"
      @current-change="currentChange"
    ></el-pagination>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%" close-on-click-modal>
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="confirmDel"> </el-button>
      </span>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="dialogFormVisible"
      :close-on-click-modal="false"
      :show-close="true"
      :destroy-on-close="true"
    >
      <div style="height:300px;width:100%;display:flex;flex-direction:column;align-items:center">
        <el-form
          label-position="right"
          :label-width="$store.state.mobile ? '80px' : '80px'"
          :model="form"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="" prop="carType">
            <el-input v-model.trim="form.carType" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="carNumber">
            <el-input v-model.trim="form.carNumber" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="register">
            <el-input v-model.trim="form.register" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="driver">
            <el-input v-model.trim="form.driver" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="  " prop="phone" width="110">
            <el-input v-model.number="form.phone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="license">
            <el-input v-model.trim="form.license" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="company">
            <el-input v-model.trim="form.company" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model.trim="form.remark" placeholder="" type="textarea"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="confirm"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/javascript">
export default {
  created() {
    this.initFormData()
    this.get()
  },
  data() {
    return {
      index: 0,
      format: {
        carType: '',
        carNumber: '',
        register: '',
        driver: '',
        phone: '',
        company: '',
        license: '',
        remark: ''
      },
      Initform: {
        id: '',
        carType: '',
        carNumber: '',
        register: '',
        driver: '',
        phone: '',
        company: '',
        license: '',
        remark: ''
      },
      form: {},
      oldForm: {},
      type: 'add',
      total: 1,
      searchMsg: {
        carType: '',
        carNumber: '',
        register: '',
        company: '',
        driver: '',
        phone: '',
        i: 1
      },
      tableData: [],
      dialogVisible: false,
      dialogFormVisible: false,
      rules: {
        carType: [
          { required: true, message: '', trigger: 'blur' }
        ],
        carNumber: [
          { required: true, message: '', trigger: 'blur' }
        ],
        register: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', message: '', trigger: 'blur' }
        ],
        driver: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    get() {
      this.$req('car', this.searchMsg, {
        method: 'get'
      }).then(data => {
        this.tableData = data.res
        this.total = data.total
      })
    },
    search() {
      this.searchMsg.i = 1
      this.get()
    },
    add() {
      this.type = 'add'
      this.initFormData()
      this.dialogFormVisible = true
    },
    change(i) {
      this.index = i
      this.type = 'change'
      this.tableData[i].phone = parseInt(this.tableData[i].phone)
      this.form = this.copy(this.tableData[i])
      this.oldForm = this.copy(this.form)
      this.dialogFormVisible = true
    },
    remove(i) {
      this.index = i
      this.dialogVisible = true
    },
    confirm() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          if (this.form.phone.toString().length !== 11) {
            return this.$message.error('11')
          }
          if (this.type === 'add') {
            this.$req('car/add', this.form).then(data => {
              if (data.status === 1) {
                this.$message({
                  message: '',
                  type: 'success'
                })
                this.tableData.unshift(data.res)
                this.initFormData()
              } else if (data.status === 0) {
                this.$message.error('')
              }
            })
          } else {
            this.$req('car/update', this.form).then(data => {
              if (data.status === 1) {
                this.$message({
                  message: '',
                  type: 'success'
                })
                this.tableData.splice(this.index, 1, this.form)
              } else if (data.status === 0) {
                this.$message.error('')
              }
            })
          }
        }
      })
    },
    cancel() {
      this.dialogFormVisible = false
    },
    confirmDel() {
      this.$req('car/delete', {
        id: this.tableData[this.index].id
      }).then(data => {
        if (data.status === 1) {
          this.$message({
            message: '',
            type: 'success'
          })
          this.tableData.splice(this.index, 1)
        } else if (data.status === 0) {
          this.$message.error('')
        }
        this.dialogVisible = false
      })
    },
    currentChange(i) {
      this.searchMsg.i = i
      this.get()
    },
    initFormData() {
      this.form = this.copy(this.Initform)
    }
  }
}
</script>
